@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.stars_cont_rating{
    display: flex;
    flex-flow: row wrap;
    margin: 0px;
    align-items: center;
}



.stars_cont {
    display: flex;
    flex-flow: row nowrap;
}

.stars {
    display: flex;
    flex-flow:row nowrap;
    margin: auto 3px;
}

.stars > div{


    display: flex;
    flex-flow:row nowrap;
    margin: auto 3px;
}

.Comentario_item{
    margin: 20px 5px;
    display: flex;
    flex-flow: column wrap;
    box-shadow: 0 0 3px 2px rgba(1,1,1,0.4);

}

.Comentario_item  .cabezera_coment {

    padding: 5px 0px;
    margin:5px;

    background-color: rgb(168, 233, 219);


    display: flex;
    flex-flow: row wrap;

    justify-content:space-around;
    align-items: center;


}

.Comentario_item  .cabezera_coment  div  {
    display: flex;
    flex-flow:  row wrap;
    align-items: center;

}


.comentario_texto{
    min-height: 25px;
    background-color: azure;
    border-radius: 0px 0px 5px  5px;

    padding: 5px 0px;
    margin: auto 0px;

}


.img_user{
    width: 40px;
    height: 40px;
}

.Cont_prin_detalles{
    height: 650px;
    overflow-y: scroll;
}
.Cont_detalles_imagenes{
    display: flex;
    flex-flow: row wrap;
    margin: auto;

    justify-content: center;
    align-items: center;

    padding: 5px;
    overflow: hidden;

}
.img_detalles{
    overflow: hidden;

    margin: 3px;

    box-shadow: 0px 0px 1px 2px rgba(1,1,1,0.4);

    width: 230px;
    height: 200px;

}
.img_not_found{
    max-width: 400px;
    max-height: 350px;
      
    width: 90%;
    height: 90%;
}
.Cont_detalles_horarios{
    text-align: left;
    display: flex;
    flex-flow: row wrap;
}

.page_loader{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e3e3e3;
    overflow: hidden;
}
.page_loader span{
    width: 50px;
    height: 50px;
    border: 6px solid rgba(0, 0, 0, 0.459);
    border-left: 6px solid rgb(116, 90, 90);
    border-radius: 50%;
    animation: page_loader 1s infinite linear;
}


@keyframes page_loader {
    to {
        transform: rotate(360deg);
    }
}
.Cont_prin_Lugares_cercanos{
    overflow-y: scroll;
    height: 650px;
}
.lugares_item{
  margin: 30px;
  padding: 15px 5px;
  background-color: #e3e3e3;
  border-radius: 3px;
  box-shadow: 0 0 3px 2px rgba(1,1,1,0.4);

}

.lugares_item > div button{
    padding: 5px 25px;
    background-color: #2195d8;
    border: none;


    font-weight: 800;
    padding: 5px 20px;
    box-shadow: 0px 1px 2px 2px rgba(1,1,1,0.4);

    transition: all 0.3s;
}
.lugares_item > div button:hover{
  transform: scale(1.1);
  background-color: rgb(23, 184, 212);
}

.lugares_item img{
  max-width: 400px;
  max-height: 350px;

  width: 90%;
  height: 90%;
}

.lugares_rating{
  margin:  0px 5%;
}


#boton_mostrar_mas{
  padding: 5px 25px;
  background-color: #314242;
  border: none;

  color: wheat;
  font-weight: 800;
  padding: 5px 20px;
  box-shadow: 0px 1px 2px 2px rgba(1,1,1,0.4);
  margin: 10px;
  transition: all 0.3s;
}
#boton_mostrar_mas:hover{
  transform: scale(1.1);
  background-color: rgb(88, 148, 158);
}

@media(max-width:500px){
  .lugares_item{
    margin: 30px 10px;
    padding: 20px 5px;  
  }
}
.container_resultados{
    width: 100%;
    margin:30px auto;

    display: flex;
    flex-flow: row nowrap;
    padding: 20px ;
  }

.resultado_item{
    width: 50%;
    max-width: 580px;
    min-width: 400px;
    height: 650px;

    margin:auto;
    background: #d7d5cd;

    overflow-y: auto;
}

.resultado_item + p{
    text-align: center;
}

.resultado_item > img{
    max-width: 300px;
    width: 90%;
    max-height: 250px;

}

.resultado_item button[class^='button']{
    margin:2px ;

    display: flex;
    margin: 15px auto;
    background-color: rgb(23, 184, 212);
    box-shadow: 0px 1px 2px 2px rgba(1,1,1,0.4);
    border: none;
    color: white;
    font-weight: 800;
    padding: 5px 20px;
    transition: all 0.3s;
}
.resultado_item button[class^='button']:hover{
    transform: scale(1.1);
    background-color: rgb(37, 154, 175);
}

.tipo_local{
    padding: 5% 10% ;
    display: flex;
    flex-flow: column wrap;
    text-align: start;
}
.tipo_local p{
  margin:0px;
}


.resultado_detalles{
    width: 96%;
    display: none;
    background-color: #e3e3e3;
    animation-duration: 1s;
    position: relative;
}
.lugares_cercanos{
  width: 96%;
  display: none;
  background-color: #e3e3e3;
  animation-duration: 1s;
  position: relative;
}


@keyframes aparece{
    from{
        width: 0%;
    }
    to{
        width: 96%;
    }
}

@keyframes desaparece{
    from{
        width: 96%;
    }
    to{
        width: 0%;
    }
}

@keyframes page_loader{
    to{
        transform: rotate(360deg);
    }
}
@media (max-width:900px){
    .container_resultados{
        width: 90%;
        margin: 30px auto;


        flex-flow: column wrap;
      }

    .resultado_item{
        width: 96%;
        min-width: auto;
        min-height: 300px;

    }
    .resultado_detalles{
        margin: auto ;
        height: auto;
        max-width: 580px;
        width: 96%;
    }
    .lugares_cercanos{
      margin: auto ;
      height: auto;
      max-width: 580px;
      width: 96%;
    }
    .tipo_local{
      padding: 5%;
    }


}


h1,h2,h3,h4,h5,h6{
  font-family: 'Fira Sans', sans-serif;
}
p,strong{
  font-family: 'Quicksand', sans-serif;
}
ul {
  list-style-type: square;
}


.container_app{
  width: 90%;
  margin: 20px auto ;

  text-align: center;

  display: flex;
  flex-flow: column wrap;
}

.container_map{
  width: 90%;
  box-shadow: 1px 1px 1px 1px rgb(92, 80, 80);
  margin: auto;

  display: flex;
  flex-flow: row wrap;

}

#map{
  margin: auto;
  width: 60%;
  height: 700px;
  box-shadow: 1px 1px 1px 1px black;

}

#buscador{
  width: 40%;
  display: flex;
  flex-flow: column nowrap;

  align-items: center;
  padding: 30px 0px;
}
#buscador input{
  margin: 10px;

  width: 70%;
  height: 28px;

  border: none;
  box-shadow: none;
  outline: none;

  border-bottom: solid #333646  1px  ;
  background-image: url(/React_proyect_maps/static/media/search-solid.cc51b702.svg);
  background-size: 20px ;
  background-position: right;
  background-repeat: no-repeat;
}
#buscador input:focus{
  background-color: none;

}

#buscador button{
  padding: 7px 35px;

  border: none;

  background-color: rgb(156, 212, 99);
  color: rgb(255, 255, 255);
}
#buscador button:hover{
  background-color: rgb(167, 204, 131);
}
#opciones{
  padding: 5px 30px;
  border-radius: 0px;
  border: 0.5px solid rgb(156, 212, 99);
}
#opciones:hover{
  background-color: rgb(167, 204, 131);
}
.Calculo_distancia{
  margin: 5px;
}
@media (max-width:900px){
  .container_app{
    margin-top: 0px;
    width: 100%;
  }
  .container_map{
    width: 100%;
    box-shadow: none;
    flex-flow: column wrap;
  }
  .container_resultados{
    padding: 0px;
  }


  #map{
    width: 100%;
    height: 300px;
  }
  #buscador{
    margin-bottom: 100px;
    width: 100%;
  }
  #buscador input{
    width: 85%;
    max-width: 300px;
  }



}

